export const getRelativeDate = (
  date: number,
  from: Date = new Date()
): Date => {
  const tmp = new Date(from);

  return new Date(tmp.setDate(tmp.getDate() + date));
};

export const getRelativeMonth = (
  month: number,
  from: Date = new Date()
): Date => {
  const tmp = new Date(from);

  return new Date(tmp.setMonth(tmp.getMonth() + month));
};

export const getRelativeYear = (
  year: number,
  from: Date = new Date()
): Date => {
  const tmp = new Date(from);

  return new Date(tmp.setFullYear(tmp.getFullYear() + year));
};

export const getRemovedHMS = (date: Date | number): Date => {
  const tmp = date instanceof Date ? date : new Date(date);

  tmp.setHours(0);
  tmp.setMinutes(0);
  tmp.setSeconds(0);

  return tmp;
};
