import { Link } from 'gatsby';
import React, { useMemo, useContext } from 'react';
import { DAY_OF_THE_WEEK } from '@constants/time';
import { getRemovedHMS } from '@utils/date';
import Context from '../../contexts/context';
import {
  ActivityListTitle,
  ActivityListTitleCircle,
  ActivityListTitleContent,
  ActivityLayout,
  ActivityList,
  ActivityItem,
  ActivityItemTagItem,
  ActivityItemTagList,
  HighlightKeyword,
} from './Styles';

interface ActivityProps {
  matchFiles: MatchedFileType[];
  repoName: string;
}

function Activity({ matchFiles, repoName }: ActivityProps) {
  const { stateFilterReducer } = useContext(Context);

  const { date, ext, keyword } = stateFilterReducer;

  /**
   * 날짜별 포스트 분류
   */
  const divideByDate = useMemo<[number, MatchedFileType[]][]>(() => {
    const tmp = new Map<number, MatchedFileType[]>();

    matchFiles.forEach((file) => {
      const {
        relativeDirectory,
        name,
        fields: {
          stampObject: { created },
        },
      } = file;

      const fileDate = getRemovedHMS(created * 1000);
      const msDate = fileDate.getTime();

      /**
       * 확장자, 검색어, 날짜 정보로 필터링
       */
      if (ext && file.ext !== ext) {
        return;
      }

      if (!(relativeDirectory + name + file.ext).includes(keyword)) {
        return;
      }

      if (date && date.toDateString() !== fileDate.toDateString()) {
        return;
      }

      if (!tmp.has(msDate)) {
        tmp.set(msDate, []);
      }

      tmp.get(msDate)?.push(file);
    });

    tmp.forEach((files) => {
      files.sort(
        (a, b) => a.fields.stampObject.created - b.fields.stampObject.created
      );
    });

    return [...tmp.entries()].sort((a, b) => b[0] - a[0]);
  }, [matchFiles, stateFilterReducer]);

  return (
    <ActivityLayout>
      {divideByDate.map(([time, files]) => {
        const date = new Date(time);

        return (
          <div>
            {/** dummy post list Item */}
            <ActivityList>
              <ActivityItem>ㅤ</ActivityItem>
            </ActivityList>

            {/** post list title */}
            <ActivityListTitle>
              <ActivityListTitleCircle>
                {DAY_OF_THE_WEEK[date.getDay()]}
              </ActivityListTitleCircle>
              <ActivityListTitleContent>
                <span>
                  {`${date.getFullYear()}년 ${
                    date.getMonth() + 1
                  }월 ${date.getDate()}일`}
                </span>
              </ActivityListTitleContent>
            </ActivityListTitle>

            {/** post list */}
            <ActivityList>
              {files.map((file) => {
                const { relativeDirectory, name, ext, markdownData } = file;

                const relativePath = relativeDirectory
                  ? `${relativeDirectory}/${name + ext}`
                  : name + ext;

                return (
                  <ActivityItem>
                    <Link to={`/post/${relativePath}`}>
                      {relativePath
                        .split(new RegExp(`(${keyword})`))
                        .map((v) => {
                          if (v === keyword) {
                            return <HighlightKeyword>{v}</HighlightKeyword>;
                          }

                          return v;
                        })}
                    </Link>
                    {markdownData && (
                      <ActivityItemTagList>
                        {markdownData.frontmatter.tag &&
                          markdownData.frontmatter.tag.map((name) => (
                            <ActivityItemTagItem>#{name}</ActivityItemTagItem>
                          ))}
                      </ActivityItemTagList>
                    )}
                  </ActivityItem>
                );
              })}
            </ActivityList>
          </div>
        );
      })}
    </ActivityLayout>
  );
}

export default Activity;
