import React, { useContext } from 'react';
import loadable from '@loadable/component';
import { getRelativeDate } from '@utils/date';
import { DAY_OF_THE_WEEK } from '@constants/time';
import { color } from '@styles';
import { WeeklyActivityChartLayout } from './Style';
import Context from '../../../contexts/context';
import * as ACTION_TYPES from '../../../store/actions/actionsType';

const LoadableChart = loadable(() => import('react-apexcharts'));

interface WeeklyActivityChartProps {
  countOfDates: Map<string, number>;
}

interface LineChartDataType {
  x: number;
  y: number;
}

interface LineChartSeriesType {
  data: LineChartDataType[];
}

function WeeklyActivityChart({ countOfDates }: WeeklyActivityChartProps) {
  const { dispatchFilterReducer } = useContext(Context);

  const today = new Date();

  const series: LineChartSeriesType[] = [{ data: [] }];

  for (let i = 0; i < 7; i += 1) {
    const prevDate = getRelativeDate(-i, today);

    series[0].data.push({
      x: prevDate.getTime(),
      y: countOfDates.get(prevDate.toDateString()) || 0,
    });
  }

  return (
    <WeeklyActivityChartLayout>
      <LoadableChart
        type="line"
        width="100%"
        height="100%"
        series={series}
        options={{
          chart: {
            id: 'activityWeeklyActivityChart',
            toolbar: {
              show: false,
              tools: {
                zoom: false,
                zoomout: false,
                pan: false,
              },
            },
            events: {
              markerClick: (
                event,
                chartContext,
                { seriesIndex, dataPointIndex, w }
              ) => {
                const { x, y } = series[seriesIndex].data[dataPointIndex];

                dispatchFilterReducer({
                  type: ACTION_TYPES.UPDATE_FILTER.CHANGE_DATE,
                  date: new Date(x),
                });
              },
            },
          },
          stroke: {
            width: 3,
          },
          markers: {
            size: 4,
            colors: ['#ffffff'],
            strokeWidth: 3,
            strokeColors: color.PRIMARY,
            hover: {
              sizeOffset: 2,
            },
          },
          colors: [color.PRIMARY],
          xaxis: {
            tooltip: {
              enabled: false,
            },
            labels: {
              formatter: (value: string) => {
                const date = new Date(value);

                return date.getDay() === today.getDay()
                  ? '오늘'
                  : DAY_OF_THE_WEEK[date.getDay()];
              },
            },
            axisTicks: {
              show: false,
            },
          },
          yaxis: {
            show: false,
          },
          dataLabels: {
            enabled: false,
          },
          grid: {
            padding: {
              top: 30,
              left: 30,
              right: 30,
              bottom: 30,
            },
            yaxis: {
              lines: {
                show: false,
              },
            },
            xaxis: {
              lines: {
                show: true,
              },
            },
          },
          tooltip: {
            x: {
              show: false,
            },
            y: {
              formatter: (_, { seriesIndex, dataPointIndex }) => {
                const { data } = series[seriesIndex] as LineChartSeriesType;
                const { x, y } = data[dataPointIndex];

                const date = new Date(x);

                return `${date.getFullYear()}년 ${
                  date.getMonth() + 1
                }월 ${date.getDate()}일 ${
                  DAY_OF_THE_WEEK[date.getDay()]
                }요일 : ${y}개`;
              },
              title: {
                formatter: () => '',
              },
            },
            marker: {
              show: false,
            },
          },
        }}
      />
    </WeeklyActivityChartLayout>
  );
}

export default WeeklyActivityChart;
