import React, { useReducer } from 'react';
import { graphql, PageProps } from 'gatsby';

import Card from '@components/Card';

import DonutChart from '@components/Chart/DonutChart';
import HeatmapChart from '@components/Chart/HeatmapChart';
import WeeklyActivityChart from '@components/Chart/WeeklyActivityChart';
import SearchBar from '@components/SearchBar';
import Activity from '@components/Activity';

import styled from 'styled-components';

import * as filterReducer from '../store/reducers/filterReducer';
import Context from '../contexts/context';

interface IndexPageProps {
  allFile: {
    nodes: FileType[];
  };
  allMarkdownRemark: {
    nodes: MarkdownRemarkType[];
  };
}

function Index({
  data: { allFile, allMarkdownRemark },
  pageContext,
}: PageProps<IndexPageProps, PageContextType>) {
  /**
   * allMarkdownRemark로 가져온 데이터를 slug를 키 값으로 하여 Map 자료구조로 저장.
   */
  const markdownNodeDict = new Map<string, MarkdownRemarkType>();

  allMarkdownRemark.nodes.forEach((node) => {
    markdownNodeDict.set(node.fields.slug, node);
  });

  /**
   * 모든 파일에 대해 '확장자 없는 상대경로'를 구한 뒤
   * 마크다운 파일이 존재하는 파일의 경우 마크다운 frontmatter 데이터를
   * 매칭시켜 저장.
   */
  const matchFiles: MatchedFileType[] = allFile.nodes.map((node) => {
    const { relativeDirectory, name } = node;

    const relativePathNoExt =
      relativeDirectory === '' ? name : `${relativeDirectory}/${name}`;

    const mdData = markdownNodeDict.get(relativePathNoExt);

    return {
      ...node,
      markdownData: mdData,
    };
  });

  /**
   * filter 상태관리
   */
  const [stateFilterReducer, dispatchFilterReducer] = useReducer(
    filterReducer.reducer,
    filterReducer.initialState
  );

  /**
   * 확장자 파일 개수 count
   */
  const countOfExts = new Map<string, number>();

  allFile.nodes.forEach(({ ext }) => {
    const count = countOfExts.get(ext);
    countOfExts.set(ext, count ? count + 1 : 1);
  });

  /**
   * 날짜별 파일 개수 count
   */
  const countOfDates = new Map<string, number>();

  allFile.nodes.forEach(({ fields }) => {
    if (!fields.stampObject) {
      return;
    }

    const date = new Date(fields.stampObject.created * 1000);
    const key = date.toDateString();
    const count = countOfDates.get(key);
    countOfDates.set(key, count ? count + 1 : 1);
  });

  return (
    <main>
      <Context.Provider
        value={{
          stateFilterReducer,
          dispatchFilterReducer,
        }}
      >
        <ContentBox>
          <Card>
            <SearchBar />
          </Card>
          <Card title="언어 분포" maxWidth="33.3%">
            <DonutChart countOfExts={countOfExts} />
          </Card>
          <Card title="주간 활동량" maxWidth="66.6%">
            <WeeklyActivityChart countOfDates={countOfDates} />
          </Card>
          <Card>
            <HeatmapChart countOfDates={countOfDates} />
          </Card>
          <Card>
            <Activity matchFiles={matchFiles} repoName={pageContext.repoName} />
          </Card>
        </ContentBox>
      </Context.Provider>
    </main>
  );
}

export default Index;

export const pageQuery = graphql`
  query IndexQuery {
    allFile(filter: { ext: { ne: ".md" } }) {
      nodes {
        name
        relativeDirectory
        ext
        fields {
          stampObject {
            created
          }
        }
      }
    }
    allMarkdownRemark {
      nodes {
        fields {
          slug
        }
        frontmatter {
          title
          subtitle
          tag
        }
      }
    }
  }
`;

export const ContentBox = styled.ul`
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0;
`;
